.pizza_item {
	display: flex;
	align-items: center;
	justify-content: center;

	@media (max-width: 650px){
    flex-direction: column;
  }
	
	>img {
		width: 50%;
		@media (max-width: 650px){
			width: 400px;
		}
		@media (max-width: 450px){
			width: 300px;
		}
	}

}

.pizza_description {
	display: flex;
	flex-direction: column;
	gap: 30px;
	font-size: 20px;
	@media (max-width: 650px){
		margin-left: 25px;
	}
>h2{
	@media (max-width: 650px){
		text-align: center;
	}
}

	span {
		font-weight: 800;
	}

	.pizza_selector {
		h2 {
			font-size: 20px;
			margin: 15px 0;
			margin-left: 0;
			font-weight: 800;
		}
		display: flex;
		align-items: center;
		gap: 20px;
	}
}

.buttons {
	margin: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 42%;

	@media (max-width: 660px){
		justify-content: space-around;
		gap: 10%;
	}
	@media (max-width: 455px){
		margin: 15px;
		flex-direction: column-reverse;
		gap: 20px;
	}
}

.pizza__active {
	background-color: green;
}
